import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'Subhadeep Jasu';
  externalComponentOpen = false;
  mode = -1;

  openRouterView(mode: number): any {
    this.mode = mode;
    this.externalComponentOpen = true;
  }
  closeRouterView(): any {
    this.externalComponentOpen = false;
  }
}
